  }

  75% { transform: rotate(-10deg);
  }
}

.wiggle {
  animation: wiggle 2s infinite steps(4);
}

@keyframes zoom {
  0%, 50%, 100% { transform: scale(1);
  }

  25% { transform: scale(1.05);
  }

  75% { transform: scale(.9);
  }
}

.zoom-0 {
  animation: zoom 4s infinite steps(8);
}

.zoom-1 {
  animation: zoom 8s infinite steps(16);
}

.zoom-2 {
  animation: zoom 8s infinite steps(16);
  animation: wiggle 4s infinite steps(8);
