.eyes {
  position: relative;
}

.eyes:before {
  position: absolute;
  content: ''; /* this is important */
  height: 54px;
  width: 80px;
  left: 110%;
  top: 50%;
  margin-top: -27px;
  z-index: 2;
  background: url(../../images/squiggles/eyes.gif) bottom no-repeat;
  background-size: 100% auto;
  opacity: 0;
  transition: opacity 600ms ease-in-out;
}

.eyes:hover:before {
  opacity: 1;
}

.tiny-cloud {
  position: relative;
}

.tiny-cloud:before {
  position: absolute;
  content: ''; /* this is important */
  height: 220px;
  width: 220px;
  left: 50%;
  top: 50%;
  transform:translateX(-50%) translateY(-50%);
  z-index: 2;
  background: url(../../images/squiggles/tiny-cloud.gif) bottom no-repeat;
  background-size: 100% auto;
  opacity: 1;
  transition: opacity 600ms ease-in-out;
}

.tiny-cloud:hover:before {
  opacity: 0;
}

