  position: absolute;
  content: ''; /* this is important */
  height: 170px;
  width: 190px;
  left: 50%;
  top: 50%;
  transform:translateX(-50%) translateY(-50%);
  z-index: 2;
  background: url(../../images/squiggles/glow-2.gif) bottom no-repeat;
  background-size: 100% auto;
  opacity: 0;
  transition: opacity 600ms ease-in-out;
}

.glow-small:hover:before {
  opacity: 1;
}

.glow {
  position: relative;
}

.glow:after, .glow:before {
  position: absolute;
  content: ''; /* this is important */
  height: 190px;
  width: 70px;
  top: 50%;
  margin-top: -95px;
  z-index: 2;
  background: url(../../images/squiggles/glow-1.gif) bottom no-repeat;
  background-size: 100% auto;
}

.glow:after {
  left: -90px;
}

.glow:before {
  transform:scaleX(-1);
  right: -90px;
}

.squiggle {
  position: relative;
}

.squiggle:after {
  position: absolute;
  content: ''; /* this is important */
  height: 20px;
  width: 150px;
  left: 50%;
  margin-left: -75px;
  bottom: -16px;
  z-index: 2;
  background: url(../../images/squiggles/line-1.gif) bottom no-repeat;
  background-size: 100% auto;
  opacity: 0;
  transition: opacity 600ms ease-in-out;
}

.squiggle:hover:after {
  opacity: 1;
}

#sideNav {
  transition: width 500ms ease-in-out;
  width: 0px;
}

#main {
  width: 100%;
  transition: width 500ms ease-in-out, opacity 2s ease-in-out;
}

@media (min-width: 769px) {
  #sideNav.active {
    width: 300px;
  }

  #main.active {
    width: calc(100% - 300px);
  }
}

button:focus, button:active {
